.lds-ripple {
  width: 80px;
  height: 80px;
  margin: auto;
  position: relative;
}

.lds-ripple div {
  opacity: 1;
  border: 4px solid #fff;
  border-radius: 50%;
  animation: 1s cubic-bezier(0, .2, .8, 1) infinite lds-ripple;
  position: absolute;
}

.lds-ripple div:nth-child(2) {
  animation-delay: -.5s;
}

@keyframes lds-ripple {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
    top: 36px;
    left: 36px;
  }

  4.9% {
    width: 0;
    height: 0;
    opacity: 0;
    top: 36px;
    left: 36px;
  }

  5% {
    width: 0;
    height: 0;
    opacity: 1;
    top: 36px;
    left: 36px;
  }

  100% {
    width: 72px;
    height: 72px;
    opacity: 0;
    top: 0;
    left: 0;
  }
}

